import React, { useEffect, useState, useRef } from 'react'
import { connect } from 'react-redux';
import style from './style.module.css'

import Year from '../IndexPage/drawerPage/stream/yearStream';
import getCountData from '../../redux/reducers/countData';

let end = new Date(),
  year = end.getFullYear(),
  start = new Date(year, 0, 1) - 0

function Index (props) {
  const [state, setState] = useState({ start: start, end: end.getTime() })
  let count = {
    balance: 0,
    income: 0,
    pay: 0
  }
  let list = [...getCountData('getCountData', { type: 'getCountData', data: { list: 'all', demand: { date: [start, end] } } })]
  useEffect(() => {
    getStream()
  }, [])
  // 进入年清单
  const getStream = () => {
    let end = new Date(),
      year = end.getFullYear(),
      start = new Date(year, 0, 1) - 0
    setState({ ...state, start, end: end.getTime() })
  }


  // 计算结余金额
  const sumCount = () => {
    count.balance = count.income = count.pay = 0
    if (list.length && list[0].pay) {
      list.map(item => {
        if (item.pay) {
          count.pay += item.count * 1
        } else {
          count.income += item.count * 1
        }
      })
    }
    count.balance = count.income - count.pay
    return (
      <div className={style.countHead}>
        <div className={style.count}><span>{count.balance.numberFormat(2)}</span><div style={{ textAlign: 'left' }}>结余</div></div>
        <div className={style.count}><span>{count.income.numberFormat(2)}</span><div style={{ textAlign: 'center' }}>收入</div></div>
        <div className={style.count}><span>{count.pay.numberFormat(2)}</span><div style={{ textAlign: 'right' }}>支出</div></div>
      </div>
    )
  }

  return (
    <div className={style.container}>
      <div className={style.head}>
      </div>
      {/* 金额汇总区域 */}
      {sumCount()}
      {/* 展示区域 */}
      <div className={style.displayArea}>
        <Year week={false} timeSeg={state}></Year>
      </div>
    </div>
  )
}
export default connect()(Index)
